'use client';

import { useDrag } from 'react-dnd';
import { Task } from './Board';

interface TaskCardProps {
  task: Task;
  columnId: string;
  onDelete: () => void;
}

export default function TaskCard({ task, columnId, onDelete }: TaskCardProps) {
  const [{ isDragging }, drag] = useDrag({
    type: 'TASK',
    item: { id: task.id, columnId },
    collect: (monitor: any) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  return (
    <div
      ref={drag}
      className={`bg-gray-50 p-3 rounded shadow-sm cursor-move ${isDragging ? 'opacity-50' : ''}`}
    >
      <p className="text-sm">{task.content}</p>
      <button
        onClick={onDelete}
        className="mt-2 text-red-500 hover:text-red-700 text-xs"
      >
        Delete
      </button>
    </div>
  );
}
