'use client';

import { useState, useEffect } from 'react';
import Column from './Column';

export interface Task {
  id: string;
  content: string;
}

export interface ColumnData {
  id: string;
  title: string;
  tasks: Task[];
}

const initialColumns: ColumnData[] = [
  {
    id: 'todo',
    title: 'To Do',
    tasks: [
      { id: '1', content: 'Learn React' },
      { id: '2', content: 'Build a kanban board' },
    ],
  },
  {
    id: 'in-progress',
    title: 'In Progress',
    tasks: [
      { id: '3', content: 'Write tests' },
    ],
  },
  {
    id: 'done',
    title: 'Done',
    tasks: [
      { id: '4', content: 'Set up project' },
    ],
  },
];

export default function Board() {
  const [columns, setColumns] = useState<ColumnData[]>(initialColumns);

  useEffect(() => {
    const saved = localStorage.getItem('kanban-board');
    if (saved) {
      setColumns(JSON.parse(saved));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem('kanban-board', JSON.stringify(columns));
  }, [columns]);

  const moveTask = (taskId: string, fromColumnId: string, toColumnId: string) => {
    setColumns(prev => {
      const newColumns = [...prev];
      const fromColumn = newColumns.find(col => col.id === fromColumnId);
      const toColumn = newColumns.find(col => col.id === toColumnId);
      const task = fromColumn?.tasks.find(t => t.id === taskId);
      if (fromColumn && toColumn && task) {
        fromColumn.tasks = fromColumn.tasks.filter(t => t.id !== taskId);
        toColumn.tasks.push(task);
      }
      return newColumns;
    });
  };

  const addTask = (columnId: string, content: string) => {
    const newTask: Task = { id: Date.now().toString(), content };
    setColumns(prev =>
      prev.map(col =>
        col.id === columnId ? { ...col, tasks: [...col.tasks, newTask] } : col
      )
    );
  };

  const deleteTask = (taskId: string, columnId: string) => {
    setColumns(prev =>
      prev.map(col =>
        col.id === columnId ? { ...col, tasks: col.tasks.filter(t => t.id !== taskId) } : col
      )
    );
  };

  return (
    <div className="flex gap-4 p-4 min-h-screen bg-gray-100">
      {columns.map(column => (
        <Column
          key={column.id}
          column={column}
          onMoveTask={moveTask}
          onAddTask={addTask}
          onDeleteTask={deleteTask}
        />
      ))}
    </div>
  );
}
